html, body , *{
  margin: 0;
  padding: 0;
  max-width: 100vw;

}

.App {
  margin: 0;
  padding: 0;
  max-width: 100vw;
}

nav { 
  background-color: #64ffda;
  min-width: 100vw;
}

a {
  padding: 0 !important;
}

.nav-wrapper {
  width: 100%;
}

.container {
  max-width: 100vw;
  margin: 0;
  padding: 0;
}
.loggedIn {
  display: flex;
  padding: 0px;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: baseline;
  align-content: space-between;
  margin: 0 35px 0 15px
}
.notLogged {
  font-weight: bolder;
}
a{
  text-decoration: none;
  color: #8d6e63 !important;
}

a:focus {
  text-decoration: none;
  outline: none;
}
a:hover {
  text-decoration: none;
  background-color: none;
  outline: none;
}

.login-page {
  margin-top: 40px;
  margin-left: 30px;
}
.signup-page {
  margin-top: 30px;
  margin-left: 30px;
}

.btn {
  margin-top: 15px;
}

.color-white {
  color:white !important;
}

.card-action button, .vehicle-buttons button, .borrow button{
  padding-left: 10px;
  padding-right: 10px;
  margin: 5px;
}
.profile-card, .vehicles-card {
  margin-top: 40px;
  margin-left: 20px;
  margin-right: -20px;
}

.map {
  margin-left: 5%;
}

.home-card {
  margin-top: 35px;
  margin-left: 35px;
}

.create-vehicle {
  background-color: #a1887f !important;
  color: #4e342e !important;
  margin-bottom: 30px !important;
}

.create-vehicle-form {
  margin-bottom: 30px;
  border-bottom: 1px solid #e0e0e0;
}

.add-btn {
  margin-left: 40%;
}

.select-radio {
  margin-left: 30px;
}

.vehicle-info, .borrow-info {
  margin-top: 20px;
  margin-left: 8px !important;
  margin-right: 8px !important;
}
